import QR from "@/common/wxqrcode.js" // 二维码生成器
export default {
	
	data() {
		return {
			img: '',
			inviteCode: '',
			disabled: false,
			address:'www.265.im/evc'
		};
	},
	onLoad() {
		if(!uni.getStorageSync("token")&&!uni.getStorageSync("SecretKey")){
			this.$base1._isLogin()
		}
		this.img = QR.createQrCodeImg('www.265.im/evc', {
			size: parseInt(300) //二维码大小  
		})
		//用户资料
		uni.request({
			url: this.baseUrl + "/member-info",
			header: {
				//除注册登录外其他的请求都携带用户token和秘钥
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res.data)
				if (this.$base1._indexOf(res.data.status)) {
					this.$base1._isLogin()
				} else if (res.data.status == 1) {
					this.inviteCode = res.data.data.InviteCode

				} else {
					uni.showToast({
						title: res.data.message,
						icon: "none"
					})
				}
			}
		})
	},
	methods: {
		saveImg() {
			//截屏绘制图片
			let self = this;
			this.disabled = true;
			var pages = getCurrentPages();
			var page = pages[pages.length - 1];
			var bitmap = null;
			var currentWebview = page.$getAppWebview();
			bitmap = new plus.nativeObj.Bitmap('amway_img');
			// 将webview内容绘制到Bitmap对象中  
			currentWebview.draw(bitmap, function() {
				console.log('截屏绘制图片成功');
				bitmap.save("_doc/a.jpg", {}, function(i) {
					console.log('保存图片成功：' + JSON.stringify(i));
					uni.saveImageToPhotosAlbum({
						filePath: i.target,
						success: function() {
							bitmap.clear(); //销毁Bitmap图片  
							uni.showToast({
								title: '保存图片成功',
								mask: false,
								duration: 1500
							});
						}
					});
					self.disabled = false;
				}, function(e) {
					console.log('保存图片失败：' + JSON.stringify(e));
					self.disabled = false;
				});
			}, function(e) {
				console.log('截屏绘制图片失败：' + JSON.stringify(e));
				self.disabled = false;
			});
		},
		copy() {
			console.log(this.address)
		
			uni.setClipboardData({
				data: this.address,
				success: function() {
					uni.showToast({
						title: '复制成功',
						icon: 'none'
					})
				}
			});
		}
	}
}
